export var checkTaskActionCreator = function (id, isDone) { return ({
    type: 'CHECK_TASK',
    id: id,
    isDone: isDone
}); };
export var removeTaskActionCreator = function (id) { return ({
    type: 'REMOVE_TASK',
    id: id
}); };
export var addTaskActionCreator = function (id, taskValue) { return ({
    type: 'ADD_NEW_TASK',
    taskValue: taskValue,
    id: id
}); };
export var redactTaskActionCreator = function (id, newValue) { return ({
    type: 'REDACT_TASK',
    id: id,
    newValue: newValue
}); };
export var removeAllCompleteActionCreator = function () { return ({
    type: 'REMOVE_COMPLETE_TASKS'
}); };
export var doAllTasksCompleteAction = function () { return ({
    type: 'DO_ALL_TASKS_COMPLETE'
}); };
